.payment-container{
    max-width: 1000px;
    margin: 100px auto;
    font-size: 30px;
    display: block;
}

.payment-form-container{
   text-align: start;
   padding-top: 20px; 
}

.payment-container label{
    font-size: 25px;
    font-weight: 500;
    margin-right: 20px;
}

.payment-container input{
    width: 100%;
    padding: 8px;
    height: 30px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.payment-container-field{
    padding: 35px 0 0 0;
}

.payment-submit{
    width: 300px;
    padding: 15px;
    margin: 50px 0 50px 0;
    background-color: #ff4141;
    color: #fff;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 20px;
}

.payment-submit:hover{
    background-color: #000;
}