.delivery-address {
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
    font-size: 30px;
  }
  
  .main-heading {
    margin-bottom: 30px;
  }
  
  .delivery-address-form {
    justify-content: space-evenly;
    width:1000px;
  }

  .content-box{
    display: flex;
  }
  
  .left-column{
    width: 50%;
    margin-right: 100px;
  }
  .right-column {
    width: 50%;
  }
  
  .left-column label{
    display: block;
  }
  .right-column label {
    display: block;
    margin-bottom: 10px;
  }
  
  .left-column input{
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    
  }
  .right-column input {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .delivery-address-submit {
    width: 600px;
    padding: 20px;
    margin: 70px 0 70px 0;
    background-color: #ff4141;
    color: #fff;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 20px;
  }
  
  .delivery-address-submit:hover {
    background-color: #000;
  }
  